import React from 'react'
import s from './Header.module.css'
import { NavLink } from 'react-router-dom'

export type MapPropsType = {
  isAuth: boolean,
  login: string | null,
}

export type DispatchPropsType = {
  setAuthLoginOut: () => void
}

const Header: React.FC<MapPropsType & DispatchPropsType> = (props) => {
  return <header className={s.header}>
    <img src='https://is4-ssl.mzstatic.com/image/thumb/Purple113/v4/ee/91/eb/ee91ebc6-f7e6-2fa2-356e-d5930900691b/AppIcon-0-0-1x_U007emarketing-0-0-0-7-0-0-sRGB-0-0-0-GLES2_U002c0-512MB-85-220-0-0.png/1200x630wa.png' alt="" />
    <div className={s.loginBlock}>
      {props.isAuth ? <div>{props.login}:<button onClick={props.setAuthLoginOut}>Log out</button></div> :
        <NavLink to={'/login'}>Login</NavLink>
      }
    </div>
  </header>
}

export default Header