import React from 'react';
import './App.css';
import HeaderContainer from './components/Header/HeaderContainer'
import Navbar from './components/Navbar/Navbar'

import {Login} from './components/Login/Login';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';

import { Route, HashRouter, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { initApp } from './Redux/App-reducer'
import { compose } from 'redux';
import Preloader from './components/Common/Preloader/Preloader';
import { AppStateType } from './Redux/Redux-store';
import { UsersPage } from './components/Users/UsersContainer';

// const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'));//'./components/Users/UsersContainer';
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));//'./components/Dialogs/DialogsContainer';
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));//'./components/Profile/ProfileContainer';

type PropsTypeMap = ReturnType<typeof mapStateToProps>
type PropsTypeDispatch = {
  initApp : () => void
}

class App extends React.Component<PropsTypeMap & PropsTypeDispatch> {
  catchAllUnhandledErrors = (e:PromiseRejectionEvent) => {
    alert('some error')
  }
  componentDidMount() {
    this.props.initApp()
    // window.addEventListener('inhandedrejection', this.catchAllUnhandledErrors)
  }

  // componentWillUnmount() {
  //   // window.removeEventListener('inhandedrejection', this.catchAllUnhandledErrors)
  // }

  render() {
    if (!this.props.init) {
      return <Preloader />
    }

    return (
      <HashRouter>
        <div className='app-wrapper'>
          <HeaderContainer />
          <Navbar />
          <div className='app-wrapper-content'>
            <React.Suspense fallback={<div>Loading...</div>}>
              <section>
                <Route path="/login" render={() => <Login />} />
                <Route path="/profile/:userId?" render={() => <ProfileContainer />} />
                <Route path="/dialogs" render={() => <DialogsContainer />} />
                <Route path="/news" component={News} />
                <Route path="/music" component={Music} />
                <Route path="/settings" component={Settings} />
                <Route path="/users" render={() => <UsersPage />} />
              </section>
            </React.Suspense>
          </div>
        </div>
      </HashRouter>
    )
  }
}

const mapStateToProps = (state : AppStateType) => ({
  init: state.app.init
})

export default compose<React.ComponentType>(connect(mapStateToProps, { initApp }))(App)
