import { usersAPI } from '../API/UsersApi'
import { updateObjArr } from '../Utils/Validators/helper'
import { usersType } from '../Types/Types'
import { InferActionType, ThunkBaseType } from './Redux-store'
import { Dispatch } from 'react'
import { ApiResponseType } from '../API/API'

let initialState = {
    users: [] as Array<usersType>,
    pagesSizes: 10,
    totalCount: 0,
    currentPage: 1,
    isFetching: false,
    followingProgress: [] as Array<number>, //array of users id
    filter: {
        term: '',
        friend: null as null | boolean
    }
}

export type initialStateType = typeof initialState

export type FilterType = typeof initialState.filter

const usersReducer = (state = initialState, action: ActionTypes): initialStateType => {
    switch (action.type) {
        case 'users/FOLLOW':
            return {
                ...state,
                users: updateObjArr(state.users, action.userId, 'id', { followed: true })
            }
        case 'users/UNFOLLOW':
            return {
                ...state,
                users: updateObjArr(state.users, action.userId, 'id', { followed: false })
            }
        case 'users/SET-USERS':
            return { ...state, users: action.users }
        case 'users/SET-CURRENT-PAGE':
            return { ...state, currentPage: action.currentPage }
        case 'users/SET-TOTAL-USERS-COUNT':
            return { ...state, totalCount: action.totalCount }
        case 'users/SET-FILTER': 
            return { ...state, filter: action.payload }
        case 'users/TOGGLE-IS-FETCHING':
            return { ...state, isFetching: action.isFetching }
        case 'users/TOGGLE-IS-FOLLOWING':
            return {
                ...state,
                followingProgress: action.isFetching ?
                    [...state.followingProgress, action.userId]
                    :
                    state.followingProgress.filter(id => id != action.userId)
            }
        default:
            return state
    }
}

type ActionTypes = InferActionType<typeof action>

export const action = {
    acceptFollow: (userId: number) => (
        {
            type: 'users/FOLLOW',
            userId
        } as const
    ),

    acceptUnfollow: (userId: number) => (
        {
            type: 'users/UNFOLLOW',
            userId
        } as const
    ),

    setUsers: (users: Array<usersType>) => (
        {
            type: 'users/SET-USERS',
            users
        } as const
    ),

    setCurrentPage: (currentPage: number) => (
        {
            type: 'users/SET-CURRENT-PAGE',
            currentPage
        } as const
    ),

    setFilter: (filter: FilterType) => (
        {
            type: 'users/SET-FILTER',
            payload: filter
        } as const
    ),

    setUsersTotalCount: (totalCount: number) => (
        {
            type: 'users/SET-TOTAL-USERS-COUNT',
            totalCount
        } as const
    ),

    toggle: (isFetching: boolean) => (
        {
            type: 'users/TOGGLE-IS-FETCHING',
            isFetching
        } as const
    ),

    toggleFollowing: (isFetching: boolean, userId: number) => (
        {
            type: 'users/TOGGLE-IS-FOLLOWING',
            isFetching,
            userId
        } as const
    )
}

type ThunkType = ThunkBaseType<ActionTypes>

export const getUsers = (page: number, pagesSizes: number, filter: FilterType): ThunkType => {
    return async (dispatch, getState) => {
        dispatch(action.toggle(true))
        dispatch(action.setCurrentPage(page))
        dispatch(action.setFilter(filter))
        let data = await usersAPI.getUsers(page, pagesSizes, filter.term, filter.friend)
        dispatch(action.toggle(false))
        dispatch(action.setUsers(data.items))
        dispatch(action.setUsersTotalCount(data.totalCount))
    }
}

const _followUnfollow = async (dispatch: Dispatch<ActionTypes>, userId: number, apiMethod: (userId: number) => Promise<ApiResponseType>, actionCreator: (userId: number) => ActionTypes) => {
    dispatch(action.toggleFollowing(true, userId))
    let response = await apiMethod(userId)
    if (response.resultCode == 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(action.toggleFollowing(false, userId))
}


export const follow = (userId: number): ThunkType => {
    return async (dispatch) => {
        let actionCreator = action.acceptFollow
        await _followUnfollow(dispatch, userId, usersAPI.follow.bind(usersAPI), actionCreator)
    }
}

export const unfollow = (userId: number): ThunkType => {
    return async (dispatch) => {
        let actionCreator = action.acceptUnfollow
        await _followUnfollow(dispatch, userId, usersAPI.unfollow.bind(usersAPI), actionCreator)
    }
}

export default usersReducer