import { profileApi } from '../API/ProfileApi'
import { stopSubmit, FormAction } from 'redux-form'
import { InferActionType, ThunkBaseType } from './Redux-store'
import { arrayStateType, profileType, photosType } from '../Types/Types'

let initialState = {
    posts: [
        { id: 1, message: 'hi66', likesCount: 666 },
        { id: 2, message: 'lol_f', likesCount: 11 }
    ] as Array<arrayStateType>,
    profile: null as profileType | null,
    status: '',
    // newPostText: ''
}

export type initialStateType = typeof initialState
type ActionsType = InferActionType<typeof actions>

const profileReducer = (state = initialState, action: ActionsType): initialStateType => {
    switch (action.type) {
        case 'profile/ADD-POST':
            return {
                ...state,
                // newPostText: '',
                posts: [
                    ...state.posts,
                    { id: 5, message: action.newPostText, likesCount: 0 }]
            }
        // case 'profile/UPDATE-NEW-POST-TEXT':
        //     return {
        //         ...state,
        //         newPostText: action.newText
        //     }
        case 'profile/SET-USER-PROFILE':
            return { ...state, profile: action.profile }
        case 'profile/SET-STATUS':
            return { ...state, status: action.status }
        case 'profile/DELETE':
            return { ...state, posts: state.posts.filter(p => p.id != action.postId) }
        case "profile/SET-PHOTO":
            return { ...state, profile: { ...state.profile, photos: action.photos } as profileType }
        default:
            return state
    }
}

export const actions = {

    deletePost: (postId: number) => (
        {
            type: 'profile/DELETE',
            postId
        } as const
    ),

    addPostActionCreator: (newPostText: string) => (
        {
            type: 'profile/ADD-POST',
            newPostText
        } as const
    ),

    setUsersProfileAccept: (profile: profileType) => (
        {
            type: 'profile/SET-USER-PROFILE',
            profile
        } as const
    ),

    setStatus: (status: string) => (
        {
            type: 'profile/SET-STATUS',
            status
        } as const
    ),

    savePhotoAc: (photos: photosType) => (
        {
            type: 'profile/SET-PHOTO',
            photos
        } as const
    )

}

type ThunkType = ThunkBaseType<ActionsType | FormAction>


export const setUsersProfile = (profile: number): ThunkType =>
    async (dispatch) => {
        let response = await profileApi.setUsersProfile(profile)
        dispatch(actions.setUsersProfileAccept(response))
    }

export const getStatus = (userId: number): ThunkType =>
    async (dispatch) => {
        let response = await profileApi.getStatus(userId)
        dispatch(actions.setStatus(response))
    }

export const updateStatus = (status: string): ThunkType =>
    async (dispatch) => {
        let response = await profileApi.updateStatus(status)
        if (response.resultCode === 0) {
            dispatch(actions.setStatus(status))
        }
    }

export const savePhoto = (file: File): ThunkType =>
    async (dispatch) => {
        let response = await profileApi.savePhoto(file)
        if (response.resultCode === 0) {
            dispatch(actions.savePhotoAc(response.data.photos))
        }
    }

export const saveProfile = (profile: profileType): ThunkType =>
    async (dispatch, getState) => {
        const userId = getState().auth.userId
        let response = await profileApi.saveProfile(profile)
        if (response.resultCode === 0) {
            if (userId != null) {
                dispatch(setUsersProfile(userId))
            } else {
                throw new Error('error')
            }
        } else {
            dispatch(stopSubmit("edit", { _error: response.messages[0] }));
            return Promise.reject(response.messages[0])
        }
    }

export default profileReducer