import React from 'react'
import Header, { MapPropsType, DispatchPropsType } from './Header'
import * as Axios from 'axios'
import { connect } from 'react-redux'
import { setAuthLoginOut } from '../../Redux/Auth-reducer'
import { AppStateType } from '../../Redux/Redux-store'

type OwnPropsType = {}

type MapDispatchPropsType = {
  setAuthLoginOut: () => void
}

// type PropsType = MapPropsType & MapDispatchPropsType

class HeaderContainer extends React.Component<MapPropsType & MapDispatchPropsType> {
  render() {
    return <Header {...this.props} />
  }
}

const mapStateToProps = (state: AppStateType) => ({
  isAuth: state.auth.isAuth,
  // userId: state.auth.userId,
  // email: state.auth.email,
  login: state.auth.login,
})

export default connect<MapPropsType,
  MapDispatchPropsType,
  OwnPropsType,
  AppStateType>(mapStateToProps, { setAuthLoginOut })(HeaderContainer)