import { instance, GetItemsType, ApiResponseType, ResultCodeEnum, ResultCodeCaptchaEnum } from "./API"
import { profileApi } from "./ProfileApi"

type MeResponseDataType = {
    id: number,
    email: string,
    login: string
}

type LoginResponseDataType = {
    userId: number
}

export const usersAPI = {
    getUsers(currentPage: number, pagesSizes: number, term: string = '', friend: null | boolean = null) {
        return instance.get<GetItemsType>(`users?page=${currentPage}&count=${pagesSizes}&term=${term}` + (friend === null ?  '' : `&friend=${friend}`))
            .then(response => response.data)
    },
    follow(userId: number) {
        return instance.post<ApiResponseType>(`follow/${userId}`).then(res => res.data)//  as Promise<ApiResponseType>
    },
    unfollow(userId: number) {
        return instance.delete(`follow/${userId}`).then(res => res.data) as Promise<ApiResponseType>
    },
    setAuthUserData() {
        return instance.get<ApiResponseType<MeResponseDataType>>(`auth/me`).then(response => response.data)
    },
    setAuthLogin(email: string, password: string, rememberMe: boolean = false, captcha: null | string = null) {
        return instance.post<ApiResponseType<LoginResponseDataType, ResultCodeEnum | ResultCodeCaptchaEnum>>(`auth/login`, { email, password, rememberMe, captcha }).then(response => response.data)
    },
    setAuthLoginOut() {
        return instance.delete(`auth/login`).then(res => res.data)
    },
    setUsersProfile(userId: number) {
        return profileApi.setUsersProfile(userId)
    }
}