import { setAuthUserData } from './Auth-reducer'
import { InferActionType } from './Redux-store'

let initialState = {
    init: false
}

export type initialStateType = typeof initialState
type ActionsType = InferActionType<typeof actions>

const appReducer = (state = initialState, action: any): initialStateType => {
    switch (action.type) {
        case 'app/SET-INIT':
            return {
                ...state,
                init: true,
            }
        default:
            return state
    }
}

// type initSucActionType = {
//     type: 'app/SET-INIT'
// }

const actions = {
    initSuc: () => ({type: 'app/SET-INIT'} as const)
}

export const initApp = () => (dispatch: any) => {
    let promise = dispatch(setAuthUserData())
    Promise.all([promise]).then(() => {
        dispatch(actions.initSuc())
    })
}

export default appReducer