import { combineReducers, createStore, applyMiddleware, compose, Action } from 'redux'
import profileReducer from './Profile-reducer'
import dialogReducer from './Dialogs-reducer'
import usersReducer from './Users-reducer'
import authReducer from './Auth-reducer'
import appReducer from './App-reducer'
import thunkMiddleware, { ThunkAction } from 'redux-thunk'
import { reducer as formReducer } from 'redux-form'
import { composeWithDevTools } from 'redux-devtools-extension';

let rootReducers = combineReducers({
    profilePage: profileReducer,
    messagesPage: dialogReducer,
    usersPage: usersReducer,
    auth: authReducer,
    form: formReducer,
    app: appReducer
})

type RootreducersType = typeof rootReducers
export type AppStateType = ReturnType<RootreducersType>

export type InferActionType<T> = T extends { [key: string]: (...args: any[]) =>  infer U } ? U : never
export type ThunkBaseType<A extends Action, R = Promise<void>> = ThunkAction<R, AppStateType, unknown, A>

// declare global {
//     interface Window {
//       __REDUX_DEVTOOLS_EXTENSION_COMPOSE__?: typeof compose;
//     }
// }
// const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(rootReducers, composeWithDevTools(applyMiddleware(thunkMiddleware)));
// const store = createStore(rootReducers, composeEnhancers(applyMiddleware(thunkMiddleware)));
export default store