import React from 'react'
import { Field, reduxForm, InjectedFormProps } from 'redux-form'
import { Input } from '../Common/Preloader/FormControl/FrontControls'
import { required } from '../../Utils/Validators/validators'
import { connect, useSelector, useDispatch } from 'react-redux'
import { setAuthLogin } from '../../Redux/Auth-reducer'
import { Redirect } from 'react-router-dom'
import style from '../Common/Preloader/FormControl.module.css'
import { createField } from '../Common/Preloader/FormControl/FrontControls'
import { AppStateType } from '../../Redux/Redux-store'

type LoginFormOwnProps = {
    captchaUrl: string | null
}

const LoginForm: React.FC<InjectedFormProps<LoginFormValuesType, LoginFormOwnProps> & LoginFormOwnProps> = ({ handleSubmit, error, captchaUrl }) => {
    return (
        <form onSubmit={handleSubmit}>
            {createField<LoginFormKeys>("Email", "email", [required], Input)}
            {createField<LoginFormKeys>("Password", "password", [required], Input, { type: "password" })}
            {createField<LoginFormKeys>(undefined, "rememberMe", [], Input, { type: "checkbox" }, "remember me")}
            {captchaUrl && <img src={captchaUrl} />}
            {captchaUrl && createField("Symbols", "captcha", [required], Input, {})}
            {
                error && <div className={style.formSummaryError}>
                    {error}
                </div>
            }
            <div className="">
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm<LoginFormValuesType, LoginFormOwnProps>({ form: 'login' })(LoginForm)

// type MapStatePropsType = {
//     // isAuth: boolean,
//     // captchaUrl: string | null
// }

// type MapLoginPropsType = {
//     setAuthLogin: (email: string, password: string, rememberMe: boolean, captcha: string) => void
// }

type LoginFormValuesType = {
    email: string,
    password: string,
    rememberMe: boolean,
    captcha: string
}

type LoginFormKeys = Extract<keyof LoginFormValuesType, string>

export const Login: React.FC = () => {

    const captchaUrl = useSelector( (state: AppStateType) => state.auth.captchaUrl)
    const isAuth = useSelector( (state: AppStateType) =>     state.auth.isAuth )

    const dispatch = useDispatch()

    const onSubmit = (formData: LoginFormValuesType) => {
        dispatch(setAuthLogin(formData.email,
            formData.password,
            formData.rememberMe,
            formData.captcha))
    }

    if (isAuth) {
        return <Redirect to={'/profile'} />
    }

    return <div className="">
        <h1>login</h1>
        <LoginReduxForm onSubmit={onSubmit} captchaUrl={captchaUrl} />
    </div>
}

