import React, { useEffect } from 'react'
import Paginator from '../Common/Preloader/Paginator/Paginator';
import User from './User';
import { FilterType, getUsers, follow, unfollow } from '../../Redux/Users-reducer';
import UsersSearchForm from './UsersSearchForm';
import { useSelector, useDispatch } from 'react-redux';
import { getUserCount, getCurrentPage, getPageUser, getFollowingProgress, getUser, getUsersFilter } from '../../Redux/Users-selectors';
import userPhoto from '../../Assets/Images/user.png'

type propsType = {
    // currentPage: number,
    // onPageChanged: (pageNumber: number) => void,
    // onFilterChanged: (filter: FilterType) => void,
    // totalCount: number,
    // pagesSizes: number,
    // users: Array<usersType>,
    // followingProgress: Array<number>,
    // follow: (userId: number) => void,
    // unfollow: (userId: number) => void,
    // userPhoto: any
}

export const Users: React.FC<propsType> = (props) => {
    
    const totalCount = useSelector(getUserCount)
    const currentPage = useSelector(getCurrentPage)
    const pagesSizes = useSelector(getPageUser)
    const followingProgress = useSelector(getFollowingProgress)
    const users = useSelector(getUser)
    const filter = useSelector(getUsersFilter)
    
    useEffect(() => {
        dispatch(getUsers(currentPage, pagesSizes, filter))
    },[])

    const dispatch = useDispatch()

    const onFilterChanged = (filter: FilterType) => {
        dispatch(getUsers(1, pagesSizes, filter))
    }

    const follow = (userId: number) => {
        dispatch(follow(userId))
    }

    const unfollow = (userId: number) => {
        dispatch(unfollow(userId))
    }

    const onPageChanged = (pageNumber: number) => {
        dispatch(getUsers(pageNumber, pagesSizes, filter))
    }

    return <div> 

        <UsersSearchForm onFilterChanged={onFilterChanged}/>
        
        <Paginator currentPage={currentPage}
            onPageChanged={onPageChanged}
            totalCount={totalCount}
            pagesSizes={pagesSizes} />
        <div>
            {
                users.map(u => <User user={u}
                    followingProgress={followingProgress}
                    unfollow={unfollow}
                    follow={follow}
                    userPhoto={userPhoto}
                    key={u.id}
                />)
            }
        </div>
    </div>
}

// export default Users