import { InferActionType } from "./Redux-store"

export type dialogsType = {
    id: number,
    name: string
}
type messageType = {
    id: number,
    message: string
}

let initialState = {
    messages: [
        { id: 1, message: 'hilol' },
    ] as Array<messageType>,
    dialogs: [
        { id: 1, name: "Valer" },
    ] as Array<dialogsType>
}

export type initialStateType = typeof initialState
type ActionsType = InferActionType<typeof actions>

const dialogReducer = (state = initialState, action: ActionsType) => {
    switch (action.type) {
        case 'dialogs/ADD-MESSAGE':
            return {
                ...state,
                messages: [
                    ...state.messages,
                    { id: 5, message: action.newMessageText }
                ]
            }
        default:
            return state
    }
}

export const actions = {
    addMessageActionCreator : (newMessageText: string)  => (
        {
            type: 'dialogs/ADD-MESSAGE',
            newMessageText
        } as const
    )
}



export default dialogReducer