import { ResultCodeEnum, ResultCodeCaptchaEnum } from '../API/API'
import { stopSubmit } from 'redux-form'
import { usersAPI } from '../API/UsersApi'
import { securityApi } from '../API/SecurityApi'
import { ThunkBaseType, InferActionType } from './Redux-store'
import { Action } from 'redux'

let initialState = {
    userId: null as number | null,
    email: null as string | null,
    login: null as string | null,
    isAuth: false as boolean,
    password: null as string | null,
    rememberMe: false as boolean,
    captchaUrl: null as string | null
}

export type initialStateType = typeof initialState

type ActionsTypes = InferActionType<typeof actions>

const authReducer = (state = initialState, action: ActionsTypes): initialStateType => {
    switch (action.type) {
        case 'auth/SET-CAP':
        case 'auth/SET-USER-DATA':
            return {
                ...state,
                ...action.payload
            }
        // case 'auth/SET-LOGIN':
        //     return {
        //         isAuth: true
        //     }
        default:
            return state
    }
}

export const actions = {
    setAuthUserDataAccept: (userId: number | null, email: string | null, login: string | null, isAuth: boolean | false) => (
        {
            type: 'auth/SET-USER-DATA',
            payload: { userId, email, login, isAuth }
        } as const
    ),

    getCaptchaUrlSuc: (captchaUrl: string) => (
        {
            type: 'auth/SET-CAP',
            payload: { captchaUrl }
        } as const
    )
}

// export function setAuthLoginAccept(email, password, rememberMe) {
//     return {
//         type: 'auth/SET-LOGIN',
//         data: { email, password, rememberMe }
//     }
// }

type ThunkType = ThunkBaseType<ActionsTypes | ReturnType<typeof stopSubmit>>

export const setAuthLogin = (email: string, password: string, rememberMe: boolean, captcha: string) : ThunkType =>
    async (dispatch) => {
        let response = await usersAPI.setAuthLogin(email, password, rememberMe, captcha)
        if (response.resultCode === ResultCodeEnum.Success) {
            dispatch(setAuthUserData())
        } else {
            if (response.resultCode === ResultCodeCaptchaEnum.CaptchaIsRequired) {
                dispatch(getCaptcha())
            }
            let message = response.messages.length > 0 ? response.messages[0] : 'some error'
            dispatch(stopSubmit('login', { _error: message }))
        }
    }

export const getCaptcha = () : ThunkType =>
    async (dispatch) => {
        const response = await securityApi.getCaptcha()
        const captchaUrl = response.url
        dispatch(actions.getCaptchaUrlSuc(captchaUrl))
    }

export const setAuthLoginOut = () : ThunkType =>
    async (dispatch) => {
        let response = await usersAPI.setAuthLoginOut()
        if (response.resultCode === ResultCodeEnum.Success) {
            dispatch(actions.setAuthUserDataAccept(null, null, null, false))
        }
    }

export const setAuthUserData = () : ThunkType =>
    async (dispatch) => {
        let response = await usersAPI.setAuthUserData()
        if (response.resultCode === ResultCodeEnum.Success) {
            let { id, email, login } = response.data
            dispatch(actions.setAuthUserDataAccept(id, email, login, true))
        }
    }


export default authReducer

